/**
 * Check if the value is null undefined ''
 *
 * @param value
 */
export const checkNotEmpty = value =>
  value === '' || value === undefined || value === null;

/**
 * @param value: number
 * @param comparator: number
 * @param value
 * @param comparator
 */
export const greaterThan = (value, comparator) => value > comparator;

/**
 * Check if a valid date with format MM/DD/YYYY
 *
 * @param {*} value: string
 * @param value
 */
export const validDate = value => {
  const dateValidationRegex = /^\d{1,2}\/\d{1,2}\/\d{4}$/;
  if (!dateValidationRegex.test(value)) return false;
  const [m, d, y] = value.split('/').map(n => Number(n));
  return (
    m === Number(new Date(y, m - 1, d).getMonth()) + 1 &&
    d === Number(new Date(y, m - 1, d).getDate())
  );
};

export const equal = (a, b) => a === b;

export const notEqual = (a, b) => !equal(a, b);

export const isNull = a => checkNotEmpty(a);

export const isNotNull = a => !isNull(a);

export const isIn = (a, b) => b.indexOf(a) > -1;

export const isNotIn = (a, b) => !isIn(a, b);
