/* eslint-disable max-lines */
import React from 'react';
import moment from 'moment';
import DSInputMask, {
  MASK_TYPES,
  MASK_PIPES,
} from '@elliemae/ds-basic/form/InputMask';
import DSInputGroup from '@elliemae/ds-basic/form/InputGroup';
import { isNull } from '@elliemae/ds-utilities/operators';
import DSDatePickerController from './DatePickerController/DatePickerController';
import DatePickerDropdown from './DatePickerDropdown';

export default class DSdatePickerImpl extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDate: this.getSafeValue(),
      inputFocused: false,
      maskFocus: false,
      primaryInput: true,
    };
  }

  state = {};

  static getDerivedStateFromProps(nextProps, prevProps) {
    if (nextProps.value !== prevProps.value) {
      return {
        selectedDate: nextProps.value ? moment(nextProps.value) : '',
      };
    }
    if (nextProps.date !== prevProps.value) {
      return {
        selectedDate: nextProps.date ? moment(nextProps.date) : '',
      };
    }

    return null;
  }

  onClickPicker = () => {
    this.setState({ inputFocused: true });
  };

  getSafeValue = () => {
    const { value, date, defaultValue } = this.props;
    const { primaryInput, selectedDate, dataInput } = this.state;
    if (value) {
      return moment(value);
    }
    if (date) {
      return moment(date);
    }
    if (defaultValue) {
      return moment(defaultValue);
    }
    if (primaryInput && dataInput) {
      return moment(dataInput.target.value);
    }
    if (selectedDate) {
      return selectedDate;
    }
    return null;
  };

  getSafeInputValue = () => {
    const { dataInput, primaryInput, maskFocus } = this.state;
    const { value } = this.props;
    const safeValue = this.getSafeValue();

    if (isNull(value)) return '';
    if (
      safeValue &&
      dataInput &&
      !maskFocus &&
      safeValue.format('MM/DD/YYYY') !== dataInput.target.value
    ) {
      return safeValue.format('MM/DD/YYYY');
    }
    if (primaryInput && dataInput) return dataInput.target.value;

    if (!safeValue) return '';
    return safeValue.format('MMDDYYYY');
  };

  isControlled = () => {
    const { value, date } = this.props;
    return Boolean(value) || Boolean(date);
  };

  handleDateChange = (date, event) => {
    const { readOnly, onDateChange, keepOpenOnDateSelect } = this.props;
    if (!readOnly) {
      if (!this.isControlled()) {
        this.setState({ selectedDate: date, primaryInput: false });
      }
      onDateChange(date);
      this.setState({ primaryInput: false });
      if (!keepOpenOnDateSelect || event.key === 'Enter')
        this.setState({ inputFocused: false });
    }
  };

  handleChange = dataInput => {
    const { onDateChange } = this.props;
    const momentvalue = moment(dataInput.target.value);
    this.setState({ selectedDate: momentvalue, dataInput, primaryInput: true });
    onDateChange(momentvalue);
  };

  handleFocus = ({ focused }) => {
    this.setState({ inputFocused: focused });
  };

  handleCalendarClose = () => {
    const { onClose } = this.props;

    this.setState({ inputFocused: false });
    this.handleOnBlur();
    onClose();
  };

  handleClickEscape = () => {
    const { onClose } = this.props;

    this.setState({ inputFocused: false });
    this.handleOnBlur();
    onClose();
  };

  handleOnBlur = () => {
    const { onBlur } = this.props;
    const { selectedDate } = this.state;

    this.setState({ maskFocus: false, dataInput: undefined });
    onBlur(selectedDate);
  };

  handleInputFocus = dataInput => {
    const { onFocus } = this.props;
    this.setState({ maskFocus: true });
    onFocus(dataInput);
  };

  render() {
    const { inputFocused } = this.state;
    const {
      disabled,
      numberOfMonths,
      readOnly,
      enableOutsideDays,
      transitionDuration,
      firstDayOfWeek,
      keepOpenOnDateSelect,
      hideKeyboardShortcutsPanel,
      onPrevMonthClick,
      onNextMonthClick,
      onClose,
      phrases,
      placeholder,
      displayFormatDay,
      isDayBlocked,
      isOutsideRange,
      isDayHighlighted,
      tabIndex,
      zIndex,
      pickerProps,
    } = this.props;
    const safedate = this.getSafeValue();
    const safeInputValue = this.getSafeInputValue();
    return (
      <DSInputGroup
        rightAddon={
          <DatePickerDropdown
            disabled={disabled}
            isOpen={inputFocused}
            menu={
              <div style={{ minHeight: '310px' }}>
                <DSDatePickerController
                  date={safedate}
                  displayFormatDay={displayFormatDay}
                  enableOutsideDays={enableOutsideDays}
                  firstDayOfWeek={firstDayOfWeek}
                  hideKeyboardShortcutsPanel={hideKeyboardShortcutsPanel}
                  isDayBlocked={isDayBlocked}
                  isDayHighlighted={isDayHighlighted}
                  isOutsideRange={isOutsideRange}
                  keepOpenOnDateSelect={keepOpenOnDateSelect}
                  numberOfMonths={numberOfMonths}
                  onClickEscape={this.handleClickEscape}
                  onClose={onClose}
                  onDateChange={this.handleDateChange(event)}
                  onFocusChange={this.handleFocus}
                  onNextMonthClick={onNextMonthClick}
                  onOutsideClick={this.handleCalendarClose}
                  onPrevMonthClick={onPrevMonthClick}
                  phrases={phrases}
                  transitionDuration={transitionDuration}
                />
              </div>
            }
            onClick={this.onClickPicker}
            readOnly={readOnly}
            zIndex={zIndex}
            pickerProps={pickerProps}
          />
        }
      >
        <DSInputMask
          data-testid="datepicker-input"
          aria-label={placeholder || 'Date Picker Input Mask'}
          disabled={disabled}
          mask={MASK_TYPES.DATE}
          onBlur={this.handleOnBlur}
          onChange={this.handleChange}
          onFocus={this.handleInputFocus}
          pipe={MASK_PIPES.AUTO_CORRECT_DATE}
          placeholder={placeholder}
          tabIndex={tabIndex}
          value={safeInputValue}
        />
      </DSInputGroup>
    );
  }
}
